@media only screen and (max-width: 767px) {
  .footer {
    padding-top: 90px;
  }
}

.footer__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 1023px) {
  .footer__logo {
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media only screen and (max-width: 767px) {
  .footer__logo {
    top: 20px;
    left: 10px;
    transform: none;
  }
}

.footer__content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

@media only screen and (max-width: 1023px) {
  .footer__content {
    align-items: flex-start;
  }
}

@media only screen and (max-width: 767px) {
  .footer__content {
    text-align: left;
  }
}

.footer__left {
  flex: 0 0 69%;
  min-width: 69%;
}

@media only screen and (max-width: 767px) {
  .footer__left {
    flex: 0 0 100%;
    min-width: 100%;
    margin-bottom: 20px;
  }
}

.footer__right {
  flex: 0 0 31%;
  min-width: 31%;
}

@media only screen and (max-width: 767px) {
  .footer__right {
    flex: 0 0 100%;
    min-width: 100%;
  }
}

.footer__row:not(:last-child) {
  margin-bottom: 40px;
}

@media only screen and (max-width: 1259px) {
  .footer__row:not(:last-child) {
    margin-bottom: 0;
  }
}

.footer__row .link:first-child {
  margin-right: 50px;
}

@media only screen and (max-width: 1259px) {
  .footer__row .link:first-child {
    margin-right: 0;
  }
}

@media only screen and (max-width: 1259px) {
  .footer__row .link {
    display: block;
  }
}

.footer .links {
  margin-bottom: 13px;
}

.link {
  font-weight: 700;
  font-size: 30px;
  letter-spacing: -0.03em;
  line-height: 1;
  margin-bottom: 5px;
}

@media only screen and (max-width: 1259px) {
  .link {
    font-size: 22px;
  }
}

.copyright {
  font-size: 30px;
  color: #999999;
}

@media only screen and (max-width: 1259px) {
  .copyright {
    font-size: 20px;
  }
}

#clip-svg {
  position: absolute;
  bottom: 0;
  left: 0;
}
