.footer {
    position: relative;
    padding: 90px 0 120px;
    background-color: $lightGrey;
    @include lg {
        padding-bottom: 40px;
        padding-top: 40px; }
    @include sm {
        padding-top: 90px; }
    &__logo {
        @include vcenter;
        @include md {
            top: 40px;
            left: 50%;
            transform: translateX(-50%); }
        @include sm {
            top: 20px;
            left: 10px;
            transform: none; } }
    &__content {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        @include md {
            align-items: flex-start; }
        @include sm {
            text-align: left; } }
    &__left {
        @include flex-size(69%);
        @include sm {
            @include flex-size(100%);
            margin-bottom: 20px; } }
    &__right {
        @include flex-size(31%);
        @include sm {
            @include flex-size(100%); } }
    &__row {
        &:not(:last-child) {
            margin-bottom: 40px;
            @include lg {
                margin-bottom: 0; } }
        .link:first-child {
            margin-right: 50px;
            @include lg {
                margin-right: 0; } }
        @include lg {
            .link {
                display: block; } } }
    .links {
        margin-bottom: 13px; } }

// .links
//     &__item
//         line-height: 1
//         margin-bottom: 5px

.link {
    font-weight: 700;
    font-size: 30px;
    letter-spacing: -0.03em;
    line-height: 1;
    margin-bottom: 5px;
    @include lg {
        font-size: 22px; } }

.copyright {
    font-size: 30px;
    color: $grey;
    @include lg {
        font-size: 20px; } }

#clip-svg {
    position: absolute;
    bottom: 0;
    left: 0; }
