//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$ermilov: 'Ermilov-Bold';
$geo: 'Geometria';
$letterica: 'AGLettericaUltraCompressed-Roman';

@font-face {
	@include fontdefault(normal, 700, $ermilov);
	@include fontpath('Ermilov-Bold', 'Ermilov-Bold', Ermilov-Bold); }

@font-face {
	@include fontdefault(normal, 700, $letterica);
	@include fontpath('AGLettericaUltraCompressed-Roman', 'AGLettericaUltraCompressed-Roman', AGLettericaUltraCompressed-Roman); }

@font-face {
	@include fontdefault(normal, 300, $geo);
	@include fontpath('Geometria-Light', 'Geometria-Light', Geometria-Light); }

@font-face {
	@include fontdefault(normal, 400, $geo);
	@include fontpath('Geometria', 'Geometria', Geometria); }

@font-face {
	@include fontdefault(normal, 500, $geo);
	@include fontpath('Geometria-Medium', 'Geometria-Medium', Geometria-Medium); }

@font-face {
	@include fontdefault(normal, 700, $geo);
	@include fontpath('Geometria-Bold', 'Geometria-Bold', Geometria-Bold); }

@font-face {
	@include fontdefault(normal, 800, $geo);
	@include fontpath('Geometria-ExtraBold', 'Geometria-ExtraBold', Geometria-ExtraBold); }

@font-face {
	@include fontdefault(normal, 900, $geo);
	@include fontpath('Geometria-Heavy', 'Geometria-Heavy', Geometria-Heavy); }
