.btn-cta {
    // width: 100%
    padding: 5px 24px 10px 20px;
    text-align: center;
    line-height: 1;
    background-color: transparent;
    background-image: $mainGradient;
    font-family: $ermilov;
    font-size: 64px;
    color: $white;
    box-shadow : 7px 7px 5px rgba(0, 0, 0, 0.3);
    letter-spacing: -0.012em;
    // cursor: pointer
    @include xl {
        font-size: 48px; }
    @include lg {
        font-size: 36px;
        padding: 10px 20px; } }


.btn-load-more {
    font-size: 0;
    background-color: transparent;
    color: $darkBlue;
    & > * {
        display: inline-block;
        vertical-align: middle; }
    &__text {
        margin-left: 10px;
        padding-bottom: 4px;
        font-family: $geo;
        font-size: 44px;
        font-weight: 700;
        border-bottom: 2px solid $darkBlue;
        @include lg {
            font-size: 30px; }
        @include sm {
            font-size: 20px; } }
    .icon {
        font-size: 48px;
        @include lg {
            font-size: 36px; }
        @include sm {
            font-size: 24px; } } }

.btn-submit {
    font-family: $ermilov;
    font-weight: 700;
    font-size: 42px;
    line-height: 1;
    letter-spacing: -0.02em;
    color: $white;
    background-image: $mainGradient;
    padding: 4px 14px 6px;
    width: 100%;
    text-align: left;
    @include lg {
        font-size: 30px; }
    @include sm {
        text-align: center; } }

.btn-back {
    font-family: $geo;
    font-weight: 700;
    font-size: 25px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    color: $darkGrey;
    background: none;
    padding: 6px 0;
    width: 100%;
    text-align: left;
    @include sm {
        text-align: center; } }


.btn-play {
    color: transparent;
    background-color: transparent;
    .icon {
        font-size: 120px;
        stroke: $white;
        stroke-width: 16px; } }

.btn-edit,
.btn-present,
.btn-notification {
    background: none;
    display: inline-block;

    .icon {
        color: rgba(#000, 0.4); }
    &.is-active {
        .icon {
            color: $red; } } }

.btn-notification {
    .icon {
        font-size: 33px; } }

.btn-edit {
    .icon {
        font-size: 27px; } }

.btn-present {
    .icon {
        font-size: 36px; } }

.link-cta {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    &_big {
        @include circle(406);
        background-color: $white;
        &:after {
            @include vcenter;
            height: 200%;
            width: 200%;
            border-radius: 50%;
            background-image: $mainGradient;
            // background-image: radial-gradient(farthest-side at 50% 50%, transparent 0%, transparent 50%, $purple 50%, $blue 100%)
            content: '';
            z-index: -1; } }
    .icon {
        font-size: 121px;
        color: $darkBlue; } }


.main-link {
    color: $darkBlue;
    font-size: 48px;
    font-family: $ermilov;
    letter-spacing: -0.06em; }
