.block {
    position: relative;
    display: block;
    @include cover;
    height: 100%;
    &__text {
        position: absolute;
        bottom: 27px;
        left: 38px;
        right: 38px;
        color: $white; }
    &__title {
        font-size: 26px;
        font-weight: 900;
        margin-bottom: 8px;
        letter-spacing: -0.02em; }
    &__org {
        font-size: 22px;
        // text-transform: uppercase
        font-weight: 700;
        letter-spacing: -0.02em; }
    &__statistics {
        display: flex;
        align-items: center; }
    .info-control {
        &:first-child {
            margin-right: 10px; } }
    &__online {
        margin-left: 18px;
        display: inline-block; }
    &__time {
        margin-left: auto; } }

.blocks {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 624px;
    @include lg {
        max-height: inherit;
        flex-direction: row; }
    .block {
        width: 25%;
        @include lg {
            width: 50%; }
        @include sm {
            width: 100%; } }
    .block_big {
        width: 50%;
        height: 624px; }
    .block_full {
        height: 624px;
        @include lg {
            height: 312px; } }

    .block_half {
        height: 312px; } }

