[data-modal] {
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	min-width: 320px;
	width: 100%;
	height: 100%;
	@include vertical;
	@include gpu;
	overflow: auto;
	transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
	text-align: center;
	&.is-open {
		opacity: 1;
		visibility: visible;
		z-index: 100; } }

[data-modal-container] {
	position: relative;
	z-index: 1;
	font-size: 14px;
	vertical-align: middle;
	padding: 10px; }
[data-modal-close] {
	position: absolute;
	background-color: transparent;
	font-size: 0;
	.icon {
		font-size: 16px; } }

.modal {
	padding-top: 120px;
	&__container {
		display: inline-flex;
		flex-wrap: wrap; }
	&__left {
		@include flex-size(30%);
		@include xl {
			@include flex-size(340px); }
		@include lg {
			display: none; }
		// +sm
 }		// 	+flex-size(100%)
	&__right {
		@include flex-size(70%);

		text-align: left;
		min-height: 600px;
		@include r(1700) {
			@include flex-size(calc(100% - 340px)); }
		@include lg {
			@include flex-size(100%); }
		.info__name {
			margin-top: -24px; } }
	&__info {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%;
		@include xl {
			padding-left: 100px; }
		@include sm {
			padding-left: 20px;
			padding-right: 20px; } }
	&__info-descr {
		font-family: $ermilov;
		font-weight: 700;
		line-height: 1.2;
		font-size: 48px;
		letter-spacing: -2.88px;
		color: $white;
		@include lg {
			font-size: 26px; } } }

.modal-card {
	border-radius: 12px;
	background-color: $white;
	height: 100%;
	// min-height: 600px
	@include sm {
		min-height: inherit; }
	&__top {
		@include clr;
		background-color: $lightGrey;
		border-radius: 12px; }
	&__tag {
		height: 116px;
		background-image: $mainGradient;
		position: relative;
		@include vertical;
		text-align: left;
		@include xl {
			height: 80px; }
		&:after {
			content: '';
			position: absolute;
			width: 0;
			height: 0;
			border-top: 58px solid transparent;
			border-bottom: 58px solid transparent;
			@include xl {
				border-top: 40px solid transparent;
				border-bottom: 40px solid transparent; } }
		span {
			display: inline-block;
			vertical-align: middle;
			font-size: 48px;
			font-family: $ermilov;
			color: $white;
			@include lg {
				font-size: 36px; }
			@include sm {
				font-size: 24px; } }
		&_left {
			border-radius: 12px 0 0 12px;
			width: 49.5%;
			span {
				padding-left: 45px; }
			&:after {
				top: 0;
				left: 100%;
				border-left: 30px solid $blue; } }
		&_right {
			border-radius: 0 12px 12px 0;
			width: 51.5%;
			float: right;
			span {
				padding-left: 97px;
				@include sm {
					padding-left: 50px; } }
			&:after {
				top: 0;
				left: 0;
				border-left: 30px solid $lightGrey; } } }

	&__bottom {
		height: calc(100% - 116px);
		display: flex;
		align-items: center;
		@include xl {
			height: calc(100% - 80px); } }
	&__content {
		width: 100%;
		padding: 26px 160px 0;
		padding-left: 160px;
		padding-top: 26px;
		padding-right: 160px;
		text-align: left;
		@include xl {
			padding: 26px 80px; }
		@include lg {
			padding: 26px 30px; } }
	&__title {
		margin-bottom: 28px;
		font-family: $ermilov;
		font-size: 48px;
		line-height: 1;
		color: $black;
		letter-spacing: -0.06em;
		@include lg {
			font-size: 30px; } }
	&__descr {
		font-weight: 700;
		font-size: 30px;
		letter-spacing: -0.02em;
		color: #333333;
		line-height: 1.2;
		margin-bottom: 80px;
		@include lg {
			font-size: 20px;
			margin-bottom: 50px; } }
	&__social {
		.social-icon {
			&:not(:last-child) {
				margin-right: 26px; } } }
	.modal-form {
		width: 100%; } }

.modal-form {
	display: inline-flex;
	flex-wrap: wrap;
	height: 100%;
	color: $black;
	font-weight: 700;
	&__left {
		@include flex-size(69%);
		padding: 40px 37px 24px 160px;
		border-radius: 0 0 12px 12px;
		box-shadow : 7px 7px 5px rgba(#000, 0.3);
		height: 100%;
		@include xl {
			padding-left: 60px; }
		@include lg {
			padding-left: 40px;
			padding-right: 20px; }
		@include sm {
			@include flex-size(100%);
			box-shadow: none;
			height: auto;
			padding: 20px; } }
	&__title {
		margin-bottom: 11px;
		font-family: $ermilov;
		font-size: 48px;
		line-height: 1;
		color: $black;
		letter-spacing: -0.06em;
		@include lg {
			font-size: 30px; } }
	&__list {
		margin-bottom: 45px; }
	&__list-item {
		margin-bottom: 3px;
		font-weight: 700;
		font-size: 18px;
		letter-spacing: -0.02em;
		line-height: 1; }
	&__group {
		margin-bottom: 18px; }
	&__inline-group {
		display: inline-block;
		@include sm {
			display: block;
			margin-bottom: 18px; }
		+ .modal-form__inline-group {
			margin-left: 48px;
			@include sm {
				margin-left: 0; } } }
	&__group-with-helper {
		position: relative;
		padding-bottom: 60px;
		@include sm {
			padding-bottom: 34px; } }
	&__label {
		display: block;
		margin-bottom: 30px;
		font-size: 25px;
		letter-spacing: -0.02em;
		color: #333333;
		@include xl {
			font-size: 20px; }
		@include md {
			font-size: 16px;
			margin-bottom: 6px; } }
	&__input {
		display: inline-block;
		position: relative;
		padding: 3px;
		font-family: $geo;
		font-weight: 700;
		background-color: transparent;
		border-bottom: 1px solid #B2B2B2;
		&_sm {
			width: 83px;
			@include sm {
				width: 60px; }
			&:not(:last-child) {
				margin-right: 20px;
				@include sm {
					margin-right: 2px; } } }
		&_lg {
			width: 270px; }
		&:focus {
			// border-bottom: 5px solid #B2B2B2
			box-shadow: 0 4px 0 0 #B2B2B2;
			@include sm {
				box-shadow: 0 2px 0 0 #B2B2B2; } } }
	&__helper {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		font-size: 18px;
		letter-spacing: -0.02em;
		color: #999999;
		@include sm {
			font-size: 14px;
			line-height: 1; } }
	&__payments-systems {
		text-align: right;
		margin-top: 10px;
		@include sm {
			display: none; } }
	&__right {
		@include flex-size(31%);
		padding-top: 55px;
		padding-left: 30px;
		@include sm {
			@include flex-size(100%);
			padding: 0 20px 30px; } }
	&__line {
		background-color: #4d4d4d;
		width: calc(100% + 30px);
		height: 90px;
		margin-left: -30px;
		margin-bottom: 57px;
		@include lg {
			margin-bottom: 36px; }
		@include md {
			margin-bottom: 12px; }
		@include sm {
			display: none; } }
	&__check-group {
		margin-bottom: 76px;
		@include sm {
			margin-bottom: 18px; } }
	&__actions-group {
		margin-top: 38px; } }


.check {
	position: relative;
	&__input {
		position: absolute;
		top: 0;
		height: 0;
		width: 0;
		opacity: 0;
		&:checked {
			+ .check__label:after {
				@include fadein; } } }
	&__label {
		display: block;
		position: relative;
		padding-left: 46px;
		font-size: 25px;
		color: #666666;
		letter-spacing: -0.02em;
		@include xl {
			font-size: 20px; }
		@include md {
			padding-left: 30px;
			font-size: 16px; }
		&:before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			height: 33px;
			width: 33px;
			border: 1px solid #b2b2b2;
			border-radius: 30%;
			@include md {
				height: 24px;
				width: 24px; } }
		&:after {
			content: "";
			position: absolute;
			bottom: 1px;
			left: 6px;
			color: $purple;
			@include fadeout(0.4s); } } }
