.info {
    color: $black;
    @include clr;
    &_white {
        color: $white; }
    &__logo {
        float: left;
        font-size: 166px;
        line-height: 1;
        padding-right: 30px;
        @include xl {
            line-height: 120px; }
        @include lg {
            line-height: 70px; }
        @include sm {
            line-height: 56px; }
        img {
            max-width: 100%; } }
    &__text {
        float: left; }
    &__name {
        font-family: $letterica;
        font-size: 166px;
        line-height: 1;
        letter-spacing: 0.02em;
        @include xl {
            font-size: 120px; }
        @include lg {
            font-size: 70px; }
        @include sm {
            font-size: 56px; } }
    &__place {
        font-family: $ermilov;
        font-size: 48px;
        margin-top: -20px;
        letter-spacing: -0.06em;
        @include lg {
            font-size: 32px; }
        @include sm {
            font-size: 20px;
            margin-top: 0; } }
    &__statistics {
        margin-top: -4px; }
    .info-control {
        &:not(:last-child) {
            margin-right: 20px;
            @include md {
                margin-right: 10px; } } } }

.info-control {
    font-size: 0;
    display: inline-block;
    vertical-align: middle;
    span {
        display: inline-block;
        vertical-align: middle; }
    &__text {
        font-weight: 800;
        font-size: 18px;
        line-height: 1.2;
        &_thin {
            font-weight: 700;
            @include md {
                font-size: 14px; }
            @include sm {
                font-size: 12px; } } }
    &__icon {
        display: inline-block;
        vertical-align: middle;
        font-size: 0; }
    .icon-circle {
        color: red;
        font-size: 20px;
        margin-right: 6px;
        @include md {
            font-size: 12px; } }
    .icon-heart {
        font-size: 22px;
        margin-right: 12px;
        @include md {
            font-size: 14px; } }
    .icon-eye {
        font-size: 14px;
        margin-right: 13px;
        @include md {
            font-size: 10px; } } }
