.header {
    position: absolute;
    top: 44px;
    left: 0;
    width: 100%;
    z-index: 5;
    // &.is-active
    //     background-color: $white
    @include xl {
        top: 20px; }
    &__inner {
        margin-left: calc(50% - 800px);
        margin-right: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &_center {
            margin-right: calc(50% - 800px); }
        @include xl {
            max-width: 1600px;
            padding: 0 10px;
            margin: 0 auto; } }
    &__logo {
        .logo__text {
            @include sm {
                display: none; } } }
    &__search {
        @include vcenter;
        width: 320px;
        z-index: 10;
        @include lg {
            position: relative;
            top: 0;
            left: 0;
            flex-grow: 1;
            padding: 0 50px;
            transform: none; }
        @include md {
            // order: 2
            // width: 34px
            padding: 0 10px;
            width: auto;
            flex-grow: 0; }
        @include sm {
            width: 30px;
            margin-left: auto;
            padding-right: 40px;
            .is-active & {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 60px;
                width: calc(100% - 70px); } } }
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include flex-size(612px);
        transition: 0.4s opacity, 0.4s visibility;
        @include xl {
            @include flex-size(auto); }
        .is-active & {
            @include sm {
                opacity: 0;
                visibility: hidden; } }
