 }    //         display: block


.logo {
    display: flex;
    align-items: center;
    color: $white;
    &_dark {
        color: $black; }
    &__image {
        min-width: 50px;
        @include xl {
            max-width: 70px; }
        @include lg {
            max-width: 50px; }
        img {
            max-width: 100%; } }
    &__text {
        position: relative;
        margin-left: 16px;
        @include lg {
            margin-left: 8px; } }

    &__name {
        font-family: $ermilov;
        font-size: 48px;
        letter-spacing: -0.057em;
        @include xl {
            font-size: 40px; }
        @include md {
            font-size: 30px; } }
    &__descr {
        position: absolute;
        left: 0;
        top: 70px;
        margin-right: -20px;
        font-weight: 300;
        font-size: 24px;
        line-height: 1.1;
        @include xl {
            font-size: 18px;
            top: 50px; }
        @include md {
            display: none; } } }

.nav {
    color: $white;
    font-size: 0;
    .header_dark & {
        color: $black; }
    &__list {
        display: flex;
        align-items: center;
        @include xl {
            margin-right: 30px; }
        @include md {
            margin-right: 12px; } }
    &__item {
        &:nth-child(1) {
            margin-right: 45px;
            @include r(1700) {
                margin-right: 30px; }
            @include r(1400) {
                margin-right: 20px; } }
        &:nth-child(2) {
            margin-right: 57px;
            @include r(1700) {
                margin-right: 30px; }
            @include r(1400) {
                margin-right: 20px; } } }
    .icon-music-circle {
        font-size: 76px;
        @include xl {
            font-size: 50px; }
        @include md {
            font-size: 40px; }
        @include sm {
            font-size: 24px; } }
    .icon-flame {
        font-size: 40px;
        @include md {
            font-size: 30px; }
        @include sm {
            font-size: 24px; } }
    .icon-crown {
        font-size: 38px;
        @include md {
            font-size: 30px; }
        @include sm {
            font-size: 24px; } } }

.user-info {
    margin-left: auto;
    color: $white;
    @include md {
        margin-right: auto; }
    &__list {
        display: flex;
        align-items: center;
        justify-content: center; }
    &__name {
        font-size: 24px;
        line-height: 1.1;
        font-weight: 300;
        @include md {
            display: none; } }
    &__avatar {
        display: block;
        position: relative;
        margin-left: 32px;
        height: 88px;
        width: 88px;
        &_colored {
            &:after {
                content: '';
                height: calc(100% + 24px);
                width: calc(100% + 24px);
                border-radius: 50%;
                @include vcenter;
                background-color: red;
                z-index: -1;
                background-image: $mainGradient;
                @include xl {
                    height: calc(100% + 12px);
                    width: calc(100% + 12px); } } }
        img {
            max-width: 100%; }
        @include xl {
            height: 60px;
            width: 60px;
            margin-left: 24px; }
        @include md {
            height: 50px;
            width: 50px;
            margin-left: 12px; } } }

.avatar {
    border-radius: 50%; }

.search {
    position: relative;
    width: 100%;
    &__input {
        width: 100%;
        background-color: transparent;
        border: 3px solid rgba($white, 0.5);
        height: 52px;
        padding: 10px 26px;
        color: $white;
        font-size: 18px;
        border-radius: 26px;
        .header_dark & {
            border: 3px solid rgba($black, 0.5);
            color: $black; }
        &:focus {
            border-color: $white;
            .header_dark & {
                border-color: $black; } }
        @include md {
            height: 40px; }
        @include sm {
            @include fadeout(0.4s);
            padding-right: 35px; }
        .is-active & {
            @include sm {
                @include fadein; } } }

    &__button {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translateY(-50%);
        background-color: transparent;
        font-size: 0;
        @include sm {
            display: none;
            right: 50%;
            transform: translate(50%, -50%); }
        .is-active & {
            @include sm {
                right: 22px;
                display: block; } }
        &_mob {
            display: none;
            @include sm {
                display: block; }
            .is-active & {
                @include sm {
                    opacity: 0; } } }
        .icon {
            color: $white;
            font-size: 24px;
            .header_dark & {
                color: $black; }
            .is-active & {
                @include sm {
                    font-size: 20px; } } } }

    &__close {
        display: none;
        background-color: transparent;
        position: absolute;
        right: -30px;
        top: -10px;
        .is-active & {
            @include sm {
                display: block; } }
        .icon {
            font-size: 16px;
            color: $white; } } }



.social-icon {
    display: inline-block;
    vertical-align: middle;
    height: 64px;
    width: 64px;
    img {
        max-width: 100%; } }

.toggle-button {
    position: relative;
    z-index: 20;
    width: 40px;
    height: 30px;
    display: none;
    @include md {
        display: block; }
    span {
        display: block;
        width: 100%;
        height: 3px;
        width: 30px;
        background: $white;
        margin: 6px auto;
        transition: all 0.3s;
        backface-visibility: hidden; } }

.toggle-button.on {
    span:nth-child(1) {
        transform: rotate(45deg) translate(4px, 10px); }
    span:nth-child(2) {
        opacity: 0; }
    span:nth-child(3) {
        transform: rotate(-45deg) translate(3px, -9px); } }

.login {
    font-size: 32px;
    letter-spacing: -0.02em;
    color: $white; }
