.hero-top {
	position: relative;
	height: 100vh;
	@include cover;
	color: $white;
	&__inner {
		height: 100%;
		@include vertical; }
	&__content {
		position: relative;
		display: inline-flex;
		vertical-align: middle;
		justify-content: space-between;
		align-items: center;
		width: 100%; }
	&__play {
		@include vcenter; }
	&__more {
		font-size: 0;
		div {
			display: inline-block;
			vertical-align: middle;
			margin-right: 10px; }
		span {
			display: block;
			font-family: $ermilov;
			font-size: 50px;
			letter-spacing: -0.02em;
			max-width: 300px;
			line-height: 1;
			&:not(:first-child) {
				margin-top: -4px; } }
		.icon {
			font-size: 118px;
			display: inline-block;
			vertical-align: middle;
			color: $white; } }
	&__bottom {
		// display: inline-block
		// vertical-align: bottom
		position: absolute;
		display: flex;
		height: 25vh;
		width: 100%;
		// max-width: 1600px
		bottom: 0;
		font-size: 20px; }
	&__details {
		@include flex-size(50%);
		text-align: left;
		display: flex;
		align-items: center;
		padding-left: calc(50% - 800px);
		span {
			display: block; } }
	&__groups {
		@include flex-size(50%);
		font-size: 0; } }


.hero-info {
	position: relative;
	h3 {
		font-family: $letterica;
		font-size: 166px;
		line-height: 1;
		letter-spacing: 0.02em;
		// +xl
 }		// 	font-size: 120px
	&__right {
		position: absolute;
		top: 50%;
		left: 110%;
		transform: translateY(-50%);
		width: 200px;
		text-align: left; }
	&__bottom {
		position: absolute;
		top: 100%;
		left: 0;
		text-align: left; }
	.info-control {
		width: 100%;
		margin-bottom: 10px; }
	&__top {
		position: absolute;
		left: 0;
		bottom: 100%;
		font-weight: 400;
		font-size: 32px;
		line-height: 1;
		letter-spacing: 0.04em; } }

.group-card {
	display: inline-block;
	width: 50%;
	height: 100%;
	position: relative;
	@include cover;
	&:after {
		content: '';
		@include coverdiv;
		background-color: rgba($black, 0.5);
		z-index: 1; }
	&__text {
		@include vcenter;
		width: 100%;
		color: $white;
		z-index: 2; } }



