.container-inner {
    max-width: 1620px;
    width: 100%;
    padding: 0 10px;
    margin: 0 auto;
    @include xl {
        max-width: 1420px; }
    @include lg {
        max-width: 1220px; } }

.col-31 {
    @include flex-size(31%);
    @include md {
        @include flex-size(100%); } }

.col-69 {
    @include flex-size(69%);
    @include md {
        @include flex-size(100%); } }
