.hero-middle {
    height: 56vh;
    min-height: 1080px;
    background-image: url(../img/hero-middle.png), $mainGradient;
    background-position: 0 100%, 0 0;
    background-repeat: no-repeat;
    position: relative;
    background-size: contain;
    @include lg {
        min-height: 800px; }
    @include md {
        min-height: 600px; }
    @include sm {
        min-height: 500px; }
    &__close {
        display: inline-block;
        background-color: transparent;
        position: absolute;
        top: 46px;
        right: 30px;
        font-size: 34px;
        @include lg {
            font-size: 20px;
            top: 20px;
            right: 10px; } }
    &__text {
        display: flex;
        flex-wrap: wrap;
        padding-top: 104px;
        @include lg {
            padding-top: 40px; } }
    &__slogan-middle {
        max-width: 840px;
        font-family: $ermilov;
        font-size: 68px;
        letter-spacing: -0.09em;
        line-height: 1;
        @include xl {
            font-size: 60px; }
        @include lg {
            font-size: 40px; }
        @include md {
            font-size: 36px; }
        @include sm {
            font-size: 24px; } }
    &__slogan-big {
        font-family: $ermilov;
        line-height: 1;
        font-size: 104px;
        color: $white;
        text-align: right;
        letter-spacing: -0.04em;
        margin-top: 14px;
        @include xl {
            font-size: 80px; }
        @include lg {
            font-size: 60px; }
        @include md {
            font-size: 40px;
            text-align: left; }
        @include sm {
            font-size: 36px; } }
    p {
        margin-top: 26px;
        max-width: 840px;
        font-size: 38px;
        letter-spacing: -0.03em;
        line-height: 1.07;
        color: $white;
        @include lg {
            font-size: 24px; }
        @include sm {
            font-size: 18px; } }
    .col-31 {
        @include md {
            order: 1; } }
    .col-69 {
        @include md {
            order: 2; } } }
