.best-section {
    &__title {
        margin: 200px auto 30px; } }

.tickets {
    padding-top: 300px;
    margin-bottom: 200px;
    &__title {
        margin-bottom: 60px; }
    &__main {
        display: flex;
        position: relative;
        margin-bottom: 60px;
        .ticket {
            position: relative;
            z-index: 3;
            &:not(:last-child) {
                margin-right: 160px; } } }
    &__cta {
        position: absolute;
        right: -185px;
        top: 50%;
        transform: translateY(-50%); } }

.likes {
    padding-bottom: 100px;
    &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px; } }


