.card-online {
    position: relative;
    height: 312px;
    @include cover;
    color: $white;
    display: flex;
    align-items: center;
    padding: 0 130px 20px;
    &__content {
        position: relative;
        .info-control {
            position: absolute;
            top: 100%;
            left: 0; } }
    &__statistics {
        position: absolute;
        left: 130px;
        bottom: 10px; } }
